<template>
	<view>
		<!-- 占位 -->
		<view v-if="showHeader" class="place"></view>
		<block v-if="module_list.length" v-for="(module,index) in module_list">
			
			
			
			<!-- 搜索 -->
			<block v-if="module.name=='search'">
				<!-- 状态栏 -->
				<view v-if="showHeader" class="status" :style="{ position: headerPosition,top:statusTop,opacity: afterHeaderOpacity}"></view>
				<!-- 顶部导航栏 -->
				<view v-if="showHeader" class="header" :style="{ position: headerPosition,top:headerTop,opacity: afterHeaderOpacity }">
					<!-- 定位城市 -->
					<view class="addr">
						<view class="icon location"></view>
						{{ city }}
					</view>
					<!-- 搜索框 -->
					<view class="input-box">
						<input placeholder="搜索商品" placeholder-style="color:#c0c0c0;" @tap="toSearch()" />
						<view class="icon search"></view>
					</view>
					<!-- 右侧图标按钮 -->
					<view class="icon-btn">
						<view class="icon yuyin-home"></view>
						<view class="icon tongzhi" @tap="toMsg"></view>
					</view>
				</view>
			</block>
			
			
			<!-- 轮播图 -->
			<block v-if="module.name=='banner'">
				<view class="swiper">
					<view class="swiper-box">
						<swiper circular="true" autoplay="true" @change="swiperChange">
							<swiper-item v-for="(item, index) in swiperList" :key="item.id">
								<image :src="item.pic_url" @tap="toSwiper(swiper)"></image>
							</swiper-item>
						</swiper>
						<view class="indicator">
							<view class="dots" v-for="(swiper, index) in swiperList" :class="[currentSwiper >= index ? 'on' : '']" :key="index"></view>
						</view>
					</view>
				</view>
			</block>
			
			<!-- 分类列表 -->
			<block v-if="module.name=='nav'">
				<u-catrgoty :cateList="categoryList" bgc="background:linear-gradient(#81F5DA,#8B9BE8)" @classifyitme="classifyitme" />
			</block>
			
			<!-- 广告图 -->
			<block v-if="module.name=='notice'">
				<view class="banner">
					<image :src="active_list"></image>
				</view>
			</block>
			
			
			<!-- 活动区 -->
			<block v-if="module.name=='miaosha'" >
				<view class="promotion" >
					<view class="text">优惠专区</view>
					<view class="list">
						<view class="column" v-for="(row, index) in Promotion" @tap="toPromotion(row)" :key="index">
							<view class="top">
								<view class="title">{{ row.title }}</view>
								<view class="countdown" v-if="row.countdown">
									<view>{{ row.countdown.h }}</view>
									:
									<view>{{ row.countdown.m }}</view>
									:
									<view>{{ row.countdown.s }}</view>
								</view>
							</view>
							<view class="left">
								<view class="ad">{{ row.ad }}</view>
								<view class="into">点击进入</view>
							</view>
							<view class="right">
								<image :src="row.img"></image>
							</view>
						</view>
					</view>
				</view>
			</block>
			
			<!-- 所有分类商品列表列表 -->
			<block v-if="module.name=='cat'">
				<!-- 导航猜你喜欢头部 -->
				
				<!-- 分类 -->
					<view class="goods-list">
						<view class="title">
							<scroll-view id="tab-bar" 
											scroll-with-animation
											class="tui-scroll-h" 
											:scroll-x="true" 
											:show-scrollbar="false" 
											:scroll-into-view="scrollInto">
								<view v-for="(tab, index) in tabBars" :key="tab.id" class="tui-tab-item" :id="tab.name" :data-current="index" @click="tabClick">
									<!-- #ifdef APP-PLUS -->
									<text class="tui-tab-item-title" :class="{ 'tui-tab-item-title-active': tabIndex == index }">{{ tab.name }}</text>
									<!-- #endif -->
							
									<!-- #ifndef APP-PLUS -->
									<view class="tui-tab-item-title" :class="{ 'tui-tab-item-title-active': tabIndex == index }">{{ tab.name }}</view>
									<!-- #endif -->
								</view>
							</scroll-view>
						</view>
						
						
						<!-- 商品item -->
						<block  v-if="tabIndex == tabIndex">
							<view class="product-list">
								<view class="product" v-for="(item,index) in tabBars[tabIndex].goods_list" :key="item.id" @tap="toGoods(item)">
									<image mode="widthFix" :src="item.pic_url"></image>
									<view class="name">{{ item.name }}</view>
									<view class="info">
										<view class="price">{{ item.price }}</view>
										<view class="slogan">售出{{item.virtual_sales}}</view>
									</view>
								</view>
							</view>
						</block>
					
					</view>
			
			</block>
			
			<!--  -->

		</block>
	</view>
</template>

<script>
	// 缓存最多页数
	const MAX_CACHE_PAGEINDEX = 3;
	// 缓存页签数量
	const MAX_CACHE_PAGE = 3;
	var ttt = 0;
	//高德SDK
	import amap from '@/common/SDK/amap-wx.js';
	import uCatrgoty from '@/components/catrgory/catrgory.vue';

	export default {
		components: {
			uCatrgoty
		},
		data() {
			return {
				tabBars: [],
				tabIndex: 0,
				scrollInto: '',
				
				module_list: [],
				showHeader: true,
				afterHeaderOpacity: 1, //不透明度
				headerPosition: 'fixed',
				headerTop: 0,
				statusTop: 0,
				nVueTitle: 0,
				page: 1,
				is_no_more: false,
				city: '北京',
				currentSwiper: 0,
				// 轮播图片
				swiperList: [],
				// 分类菜单
				categoryList: [],
				// 广告图
				active_list: '',
				Promotion: [],
				module_list: [],
			};
		},
		onPageScroll(e) {
			//兼容iOS端下拉时顶部漂移
			this.headerPosition = e.scrollTop>=0?"fixed":"absolute";
			this.headerTop = e.scrollTop>=0?null:0;
			this.statusTop = e.scrollTop>=0?null:-this.statusHeight+'px';
		},
		//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
		onPullDownRefresh() {
			setTimeout(function() {
				uni.stopPullDownRefresh();
			}, 1000);
			this.getHomeGoods()
		},
		//上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
		onReachBottom() {
			this.getHomeGoods()
		},


		onLoad() {
			// 请求首页数据
			this.getHomeGoods()
			// #ifdef APP-PLUS
			this.nVueTitle = uni.getSubNVueById('homeTitleNvue');
			this.nVueTitle.onMessage(res => {
				let type = res.data.type;
				if (type == 'focus') {
					this.toSearch();
				}
			});
			this.showHeader = false;
			this.statusHeight = plus.navigator.getStatusbarHeight();
			// #endif
			
			this.amapPlugin = new amap.AMapWX({
				//高德地图KEY，随时失效，请务必替换为自己的KEY，参考：http://ask.dcloud.net.cn/article/35070
				key: '7c235a9ac4e25e482614c6b8eac6fd8e'
			});
			//定位地址
			this.amapPlugin.getRegeo({
				success: data => {
					this.city = data[0].regeocodeData.addressComponent.city.replace(/市/g, ''); //把"市"去掉
					// // #ifdef APP-PLUS
					// this.nVueTitle.postMessage({
					// 	type: 'location',
					// 	city: this.city
					// });
					// // #endif
				}
			});
			
			//开启定时器
			this.Timer();
			//加载活动专区
			this.loadPromotion();
		},
		methods: {
			// 首页数据
			getHomeGoods() {
				var self = this;
				
				self.$request({
					url: self.$api.default.index
				}).then(res => {
					if (res.code == 0) {
						console.log(res);
						self.module_list = res.data.module_list
						self.swiperList = res.data.banner_list;
						self.categoryList = res.data.nav_icon_list
						self.active_list = res.data.block_list[0].data.pic_list[0].pic_url
						self.productList = res.data.cat_list
						self.tabBars = res.data.cat_list
					}
				
				})
			},
			
			//加载Promotion 并设定倒计时,,实际应用中应该是ajax加载此数据。
			loadPromotion() {
				let cutTime = new Date();
				let yy = cutTime.getFullYear(),
					mm = cutTime.getMonth() + 1,
					dd = cutTime.getDate();
				let tmpcountdown = yy + '/' + mm + '/' + dd + ' 23:59:59';
				let tmpPromotion = [{
						title: '整点秒杀',
						ad: '整天秒杀专区',
						img: '/static/img/s1.jpg',
						countdown: false
					},
					{
						title: '限时抢购',
						ad: '每天23点上线',
						img: '/static/img/s2.jpg',
						countdown: tmpcountdown
					} //countdown为目标时间，程序会获取当前时间倒数
				];
				//检查倒计时
				for (let i = 0; i < tmpPromotion.length; i++) {
					let row = tmpPromotion[i];
					if (row.countdown) {
						let h = '00',
							m = '00',
							s = '00';
						let currentTime = new Date();
						let cutoffTime = new Date(tmpcountdown);
						if (!(currentTime >= cutoffTime)) {
							let countTime = parseInt(
								(cutoffTime.getTime() - currentTime.getTime()) / 1000
							);
							h = parseInt(countTime / 3600);
							m = parseInt((countTime % 3600) / 60);
							s = countTime % 60;
							h = h < 10 ? '0' + h : h;
							m = m < 10 ? '0' + m : m;
							s = s < 10 ? '0' + s : s;
						}
						tmpPromotion[i].countdown = {
							h: h,
							m: m,
							s: s
						};
					}
				}
				this.Promotion = tmpPromotion;
			},
			
			
			//定时器
			Timer() {
				setInterval(() => {
					if (this.Promotion.length > 0) {
						for (let i = 0; i < this.Promotion.length; i++) {
							let row = this.Promotion[i];
							if (row.countdown) {
								if (
									!(
										row.countdown.h == 0 &&
										row.countdown.m == 0 &&
										row.countdown.s == 0
									)
								) {
									if (row.countdown.s > 0) {
										row.countdown.s--;
										row.countdown.s =
											row.countdown.s < 10 ?
											'0' + row.countdown.s :
											row.countdown.s;
									} else if (row.countdown.m > 0) {
										row.countdown.m--;
										row.countdown.m =
											row.countdown.m < 10 ?
											'0' + row.countdown.m :
											row.countdown.m;
										row.countdown.s = 59;
									} else if (row.countdown.h > 0) {
										row.countdown.h--;
										row.countdown.h =
											row.countdown.h < 10 ?
											'0' + row.countdown.h :
											row.countdown.h;
										row.countdown.m = 59;
										row.countdown.s = 59;
									}
									this.Promotion[i].countdown = row.countdown;
								}
							}
						}
					}
				}, 1000);
			},
			//消息列表
			toMsg() {
				uni.navigateTo({
					url: '/pages/msg/msg'
				})
			},
			//搜索跳转
			toSearch() {
				// uni.showToast({ title: '建议跳转到新页面做搜索功能' });
				uni.navigateTo({
					url: '/pages/search/search'
				})
			},
			//轮播图跳转
			toSwiper(e) {
				uni.showToast({
					title: e.src,
					icon: 'none'
				});
			},
			//分类跳转
			classifyitme(e) {
				uni.showToast({
					title: e.name,
					icon: "none"
				});
				uni.setStorageSync('catName', e.name);
				uni.navigateTo({
					url: '/pages/goods/goods-list/goods-list?cid=' + e.id + '&name=' + e.name
				});
				console.log(e);
			},
			//推荐商品跳转
			toPromotion(e) {
				uni.showToast({
					title: e.title,
					icon: 'none'
				});
			},
			//商品跳转
			toGoods(e) {
				console.log(e);
				uni.navigateTo({
					url: '/pages/goods/goods?&cid='+e.id
				});
			},
			//轮播图指示器
			swiperChange(event) {
				this.currentSwiper = event.detail.current;
			},
			tabClick(e) {
				console.log(e);
				let index = e.target.dataset.current || e.currentTarget.dataset.current;
				this.switchTab(index);
			},
			
			switchTab(index) {
				console.log(index);
				this.tabIndex = index;
				let scrollIndex = index - 1 < 0 ? 0 : index - 1;
				console.log(scrollIndex);
				this.scrollInto = this.tabBars[scrollIndex].name;
			},
		}
	};
</script>
<style lang="scss">
	page {
		position: relative;
		background-color: #fff;
	}

	@font-face {
		font-family: 'HMfont-home';
		src: url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2');
	}

	.pullDown-effects {
		position: fixed;
		//top: calc(100upx - 36vw);
		top: 0;
		z-index: 9;
		width: 100%;
		height: 36vw;
		/*  #ifdef  APP-PLUS  */
		padding-top: var(--status-bar-height);

		/*  #endif  */
		image {
			width: 100%;
			height: 36vw;
		}
	}

	.status {
		width: 100%;
		height: 0;
		position: fixed;
		z-index: 10;
		background-color: #fff;
		top: 0;
		left: 0;
		/*  #ifdef  APP-PLUS  */
		height: var(--status-bar-height); //覆盖样式
		/*  #endif  */
	}

	.header {
		width: 92%;
		padding: 0 4%;
		height: 100upx;
		display: flex;
		align-items: center;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 10;
		background-color: #fff;

		/*  #ifdef  APP-PLUS  */
		top: var(--status-bar-height);
		/*  #endif  */

		.addr {
			width: 120upx;
			height: 60upx;
			flex-shrink: 0;
			display: flex;
			align-items: center;
			font-size: 28upx;

			.icon {
				height: 60upx;
				margin-right: 5upx;
				display: flex;
				align-items: center;
				font-size: 42upx;
				color: #ffc50a;
			}
		}

		.input-box {
			width: 100%;
			height: 60upx;
			background-color: #f5f5f5;
			border-radius: 30upx;
			position: relative;
			display: flex;
			align-items: center;

			.icon {
				display: flex;
				align-items: center;
				position: absolute;
				top: 0;
				right: 0;
				width: 60upx;
				height: 60upx;
				font-size: 34upx;
				color: #c0c0c0;
			}

			input {
				padding-left: 28upx;
				height: 28upx;
				font-size: 28upx;
			}
		}

		.icon-btn {
			width: 120upx;
			height: 60upx;
			flex-shrink: 0;
			display: flex;

			.icon {
				width: 60upx;
				height: 60upx;
				display: flex;
				justify-content: flex-end;
				align-items: center;
				font-size: 42upx;
			}
		}
	}

	.place {
		background-color: #ffffff;
		height: 100upx;
		/*  #ifdef  APP-PLUS  */
		margin-top: var(--status-bar-height);
		/*  #endif  */
	}

	.swiper {
		width: 100%;
		margin-top: 10upx;
		display: flex;
		justify-content: center;

		.swiper-box {
			width: 92%;
			height: 30.7vw;

			overflow: hidden;
			border-radius: 15upx;
			box-shadow: 0upx 8upx 25upx rgba(0, 0, 0, 0.2);
			//兼容ios，微信小程序
			position: relative;
			z-index: 1;

			swiper {
				width: 100%;
				height: 30.7vw;

				swiper-item {
					image {
						width: 100%;
						height: 30.7vw;
					}
				}
			}

			.indicator {
				position: absolute;
				bottom: 20upx;
				left: 20upx;
				background-color: rgba(255, 255, 255, 0.4);
				width: 150upx;
				height: 5upx;
				border-radius: 3upx;
				overflow: hidden;
				display: flex;

				.dots {
					width: 0upx;
					background-color: rgba(255, 255, 255, 1);
					transition: all 0.3s ease-out;

					&.on {
						width: (100%/3);
					}
				}
			}
		}
	}

	.category-list {
		width: 92%;
		margin: 0 4%;
		padding: 0 0 30upx 0;
		border-bottom: solid 2upx #f6f6f6;
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;

		.category {
			width: 25%;
			margin-top: 50upx;
			display: flex;
			flex-wrap: wrap;

			.img {
				width: 100%;
				display: flex;
				justify-content: center;

				image {
					width: 9vw;
					height: 9vw;
				}
			}

			.text {
				margin-top: 16upx;
				width: 100%;
				display: flex;
				justify-content: center;
				font-size: 24upx;
				color: #3c3c3c;
			}
		}
	}

	.banner {
		width: 92%;
		margin: 40upx 4%;

		image {
			width: 100%;
			height: 20vw;
			border-radius: 10vw;
			box-shadow: 0upx 5upx 25upx rgba(0, 0, 0, 0.3);
		}
	}

	.promotion {
		width: 92%;
		margin: 0 4%;

		.text {
			width: 100%;
			height: 60upx;
			font-size: 34upx;
			font-weight: 600;
			margin-top: -10upx;
		}

		.list {
			width: 100%;
			display: flex;
			justify-content: space-between;

			.column {
				width: 43%;
				padding: 15upx 3%;
				background-color: #ebf9f9;
				border-radius: 10upx;
				overflow: hidden;
				display: flex;
				justify-content: space-between;
				flex-wrap: wrap;

				.top {
					width: 100%;
					height: 40upx;
					display: flex;
					align-items: center;
					margin-bottom: 5upx;

					.title {
						font-size: 30upx;
					}

					.countdown {
						margin-left: 20upx;
						display: flex;
						height: 40upx;
						display: flex;
						align-items: center;
						font-size: 20upx;

						view {
							height: 30upx;
							background-color: #f06c7a;
							display: flex;
							justify-content: center;
							align-items: center;
							color: #fff;
							border-radius: 4upx;
							margin: 0 4upx;
							padding: 0 2upx;
						}
					}
				}

				.left {
					width: 50%;
					height: 18.86vw;
					display: flex;
					flex-wrap: wrap;
					align-content: space-between;

					.ad {
						margin-top: 5upx;
						width: 100%;
						font-size: 22upx;
						color: #acb0b0;
					}

					.into {
						width: 100%;
						font-size: 24upx;
						color: #aaa;
						margin-bottom: 5upx;
					}
				}

				.right {
					width: 18.86vw;
					height: 18.86vw;

					image {
						width: 18.86vw;
						height: 18.86vw;
					}
				}
			}
		}
	}

	.goods-list {

		// background-color: #f4f4f4;
		.title {
			width: 90%;
			display: flex;
			justify-content: center;
			align-items: center;
			text-align: center;
			height: 80upx;
			color: #f47825;
			font-size: 30upx;
			margin-top: 10upx;
			border: none;
			position: relative;
			margin: 0 auto;
			image {
				width: 30upx;
				height: 30upx;
			}
			.tab-bar {
				width: 94%;
				
			}
		}

		.loading-text {
			width: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			height: 60upx;
			color: #979797;
			font-size: 24upx;
		}

		.product-list {
			width: 92%;
			padding: 0 4% 3vw 4%;
			display: flex;
			justify-content: space-between;
			flex-wrap: wrap;

			.product {
				width: 48%;
				border-radius: 20upx;
				background-color: #fff;
				margin: 0 0 15upx 0;
				box-shadow: 0upx 5upx 25upx rgba(0, 0, 0, 0.1);

				image {
					width: 100%;
					border-radius: 20upx 20upx 0 0;
				}

				.name {
					width: 92%;
					padding: 8upx 4%;
